import React from 'react';


class UserAdd extends React.Component {

    constructor () {
        super();
        this.state = {
            name:'',
            age:0,
            gender:''
        };
    }
    handleValueCHange (filed, value, type = 'string') {
        // 由于表单的值都是字符串，我们可以根据传入type为number来手动转换value的类型为number类型
        if (type == 'number') {
            value = +value;
        }

        this.setState ({
            [filed]:value
        });
    }

    handleSubmit (e) {
        e.preventDefault();
        // alert(JSON.stringify(this.state));
        const {name,age,gender} = this.state;
        fetch('http://rapapi.org/mockjsdata/23242/v1/question/page',{
            method: 'get',
        })
        .then(res => res.codeMsg)
        .then(codeMsg => {
            console.log(codeMsg);
            if (codeMsg) {
                alert('add user success');
                this.setState({
                    name: '',
                    age:0,
                    gender:''
                });
            }else {
                alert('add user failed');
            }
        })
        .catch((err) => console.error(err));
    }

    render () {
        const {name,age,gender} = this.state;
        return (
            <div>
                <header>
                    <h1>添加用户</h1>
                </header>
                
                <main>
                    <form onSubmit = {(e) => this.handleSubmit(e)}>
                        <label> 名称： </label>
                        <input type = "text" value={name} onChange={(e) => this.handleValueCHange('name',e.target.value)}/>
                        <br/>
                        <label> 年龄： </label>
                        <input type = "number" value={age} onChange={(e) => this.handleValueCHange('age',e.target.value,'number')}/>
                        <br/>
                        <label>性别： </label>
                        <select value={gender} onChange={(e) => this.handleValueCHange('gender',e.target.value)}>
                            <option value="">请选择</option>
                            <option value="male">男</option>
                            <option value="female">女</option>
                        </select>
                        <br/>
                        <br/>
                        <input type="submit" value="提交"/>
                    </form>
                </main>
             </div>
        );
    }
}

export default UserAdd;


