import React from 'react';

import 'antd-mobile/dist/antd-mobile.css';

import jsBridge from '../../Service/JSBridge';
import { List, Button, WingBlank, WhiteSpace} from 'antd-mobile';

const Item = List.Item;


const data2 = [
    // { selected: 0, label: '300Event/Mon' },
    // { selected: 1, label: '7Days/Mon' },
];

class SelectTime extends React.Component {

    constructor(props) {
        super(props);
        this.onclick = this.onclick.bind(this)
        this.state = {
            data: data2,
        };
        // this.onChange = this.onChange.bind(this)
    }
    onChange = (val) => {
        
        var data222 = this.state.data;

        for (const plan in this.state.data) {
            if (this.state.data.hasOwnProperty(plan)) {
                const element = this.state.data[plan];
                let index = this.state.data.indexOf(element);
                console.log(element.selected + element.label + 'sssssss' + val);
                if (element.label !== val) {
                    element.selected = 0;
                }else {
                    element.selected = 1;
                }
                console.log(element.selected + element.label + 'sssssss' + val);
                data222[index] = element;
            }
            
        }
        this.setState({ data: data222 })
    }
    onclick(){
        window.JSTime = new Date();
        jsBridge.send({
            service: 'StatusBar',
            action: 'style',
            data: {
                service: 'StatusBar',
                action: 'sty打撒军扩扩扩扩多所卡多啦说多啦所绿多军奥绿所扩多军绿撒军多绿军奥所绿扩多军奥绿多军绿扩多军扩绿撒军多群无飞机哦le',
            }
        });
    }
    render() {

        return (<div>
            <WingBlank>
                
                    {this.state.data.map(i => (
                    <List renderHeader={() => ''} style={{ 'borderRadius': 55 }}>
                        <Item  key={i.selected} extra={<img src={require(i.selected? './switch_on.png':'./switch_off.png')} style={{ width: '75px', height: '75px',}} alt="" />} onClick={() => this.onChange(i.label)}>
                                {i.label}
                            </Item>
                    </List>     
                    ))}

                <WhiteSpace size={'xl'} /><WhiteSpace size={'xl'} /><WhiteSpace size={'xl'} />
    
                <Button type="primary" onClick={this.onclick}>JsBridge 发送</Button> 
            </WingBlank>
            
        </div>);
    }
}


export default SelectTime;


