import React, { Component } from 'react';

import './App.css';

import Home from './comonents/Home/Home';
import About from './comonents/About/About';
import List from './comonents/List/List';
import UserAdd from './comonents/pages/UserAdd';
import SelectTime from './comonents/Home/SelectTime';

import {
  BrowserRouter as Router,
  Route,
} from 'react-router-dom';

const App = () => (
  <Router>
    <div>
      <Route path="/" component={SelectTime}/>
      <Route path="/user/add" component={UserAdd}/>
      <Route path="/user/SelectTime" component={SelectTime} />
      
    </div>
  </Router>
);

export default App;



